import React from "react";
import { apiRequest } from "../../utils/helper";
import {
  LineChart,
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip
} from "recharts";
import "./Charts.css";

export default function Charts({ date, index, type, ce, pe }) {
  const [chartData, setChartData] = React.useState([]);
  const [chartData2, setChartData2] = React.useState([]);

  React.useEffect(() => {
    const authHandler = async () => {
      try {
        const userData = await apiRequest({
          url: "https://api.optionalgo.in:6001/v1/getdata",
          method: "post",
          bodyParams: {
            date: date,
            pointtype: type,
            stockname: index,
            strike_price_pe: pe,
            strike_price_ce: ce
          }
        });
        const { data, success } = userData;
        console.log(userData);
        if (success) {
          let cData = [];

          for (let i = 0; i < data.length; i++) {
            let { time_hour_m_in_sec, indexprice, stock } = data[i];

            for (let j = 0; j < stock.length; j++) {
              let data2 = {};
              if (type !== "Both") {
                let {
                  last_price,
                  oi,
                  expiry,
                  strike_price,
                  instrument_type
                } = stock[j];
                data2 = {
                  id: time_hour_m_in_sec,
                  time: time_hour_m_in_sec,
                  indexprice: indexprice,
                  last_price: last_price.toFixed(2),
                  oi: oi,
                  expiry: expiry,
                  strike_price: strike_price,
                  instrument_type: instrument_type
                };
              } else {
                let {
                  last_price,
                  oi,
                  strike_price_pe,
                  strike_price_ce,
                  last_price_ce,
                  last_price_pe,
                  instrument_type
                } = stock[j];
                data2 = {
                  id: cData.length,
                  time: time_hour_m_in_sec,
                  last_price: last_price.toFixed(2),
                  indexprice: indexprice,
                  oi: oi,
                  strike_price_ce: strike_price_ce,
                  strike_price_pe: strike_price_pe,
                  last_price_ce: last_price_ce,
                  last_price_pe: last_price_pe,
                  instrument_type: instrument_type
                };
              }

              if (j === 0) {
                console.log(cData);
                cData.push(data2);
              }
            }
          }
          setChartData2(cData);
        }
      } catch (err) {}
    };
    authHandler();
    const interval = setInterval(() => {
      authHandler();
    }, 60000);
    return () => clearInterval(interval);
  }, [date, index, type, ce, pe]);

  React.useEffect(() => {
    setChartData([]);
    setChartData2([]);
  }, [date, index, type, ce, pe]);

  React.useEffect(() => {
    const updatechart = () => {
      if (chartData.length === 0) {
        setChartData(chartData2);
      } else {
        for (let i = chartData.length; i < chartData2.length; i++) {
          setChartData([...chartData, chartData2[i]]);
        }
      }
    };
    updatechart();
  }, [chartData2, chartData]);

  const customTooltipOnYourLine = e => {
    if (
      e.active &&
      e.payload != null &&
      e.payload[0] != null &&
      type !== "Both"
    ) {
      return (
        <div className="custom-tooltip">
          <p>{e.payload[0].payload["time"]}</p>
          <p>{e.payload[0].payload["last_price"]}</p>
          <p>{e.payload[0].payload["strike_price"]}</p>
          <p>{e.payload[0].payload["indexprice"]}</p>
        </div>
      );
    }
    if (
      e.active &&
      e.payload != null &&
      e.payload[0] != null &&
      type === "Both"
    ) {
      return (
        <div className="customTooltip">
          <div className="tooltipContent">
            <p>LTP: {e.payload[0].payload["last_price"]}</p>
            <p>Puts Price: {e.payload[0].payload["strike_price_pe"]}</p>
            <p>Call Price: {e.payload[0].payload["strike_price_ce"]}</p>
            <p>Puts Last Price: {e.payload[0].payload["last_price_pe"]}</p>
            <p>Call Last Price: {e.payload[0].payload["last_price_ce"]}</p>
            <p>Time: {e.payload[0].payload["time"]}</p>
          </div>
        </div>
      );
    } else {
      return "";
    }
  };

  return (
    <div>
      <LineChart
        width={1000}
        height={400}
        data={chartData}
        margin={{ top: 5, right: 20, bottom: 5, left: 0 }}
      >
        <Line type="monotone" dataKey="last_price" stroke="#8884d8" />
        <CartesianGrid stroke="#ccc" strokeDasharray="5 5" />
        <XAxis dataKey="time" />
        <YAxis
          type="number"
          allowDecimals={true}
          allowDataOverflow
          domain={["dataMin-10", "dataMax+10 "]}
        />
        <Tooltip content={customTooltipOnYourLine} />
      </LineChart>
    </div>
  );
}
