import React from "react";
import { apiRequest } from "../../utils/helper";
import {
  LineChart,
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip
} from "recharts";
import "./Charts.css";

export default function Charts2({ date, index, sPrice, lPrice }) {
  const [chartData, setChartData] = React.useState([]);
  const [chartData2, setChartData2] = React.useState([]);
  React.useEffect(() => {
    setChartData([]);
    setChartData2([]);
  }, [index, date]);
  React.useEffect(() => {
    const authHandler = async () => {
      try {
        console.log(date, index);
        const userData = await apiRequest({
          url: "https://api.optionalgo.in:6001/v1/getdata",
          method: "post",
          bodyParams: {
            date: date,
            pointtype: "CE",
            stockname: index
          }
        });
        const { data, success } = userData;

        if (success) {
          let cData = [];

          for (let i = 0; i < data.length; i++) {
            let { time_hour_m_in_sec, indexprice } = data[i];

            if (i === 0) {
              sPrice(indexprice);
            }
            if (i === data.length - 1) {
              lPrice(indexprice);
            }

            cData.push({ time: time_hour_m_in_sec, price: indexprice });
          }
          setChartData2(cData);
        }
      } catch (err) {}
    };
    authHandler();

    const interval = setInterval(() => {
      authHandler();
    }, 60000);
    return () => clearInterval(interval);
  }, [date, index, sPrice, lPrice]);

  React.useEffect(() => {
    const updatechart = () => {
      if (chartData.length === 0) {
        setChartData(chartData2);
      } else {
        for (let i = chartData.length; i < chartData2.length; i++) {
          setChartData([...chartData, chartData2[i]]);
        }
      }
    };
    updatechart();
  }, [chartData2, chartData]);

  return (
    <div>
      <div>
        <br />
      </div>

      <LineChart
        width={1000}
        height={400}
        data={chartData}
        margin={{ top: 5, right: 20, bottom: 5, left: 20 }}
      >
        <Line type="monotone" dataKey="price" stroke="#8884d8" />
        <CartesianGrid stroke="#ccc" strokeDasharray="5 5" />
        <XAxis dataKey="time" />
        <YAxis type="number" domain={["dataMin-50", "dataMax+50 "]} />
        <Tooltip />
      </LineChart>
    </div>
  );
}
